*{box-sizing:border-box}body{font-family:'Catamaran', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:radial-gradient(900px at 100% 110%, #212750 10%, #020409 100%);height:100vh}html,body,#root{width:100%;height:100%;margin:0;padding:0;overflow:hidden}.app{width:100%;height:100%;overflow-x:hidden}.anim{width:100vw;height:100vh;position:absolute;z-index:1}.landingPage{color:#fff;text-align:left;font-size:40px;z-index:5;width:100%;height:100vH;display:flex;align-items:center;justify-content:center}.landingPage .text-wrapper{width:1000px;padding-left:10rem;display:flex;align-items:center;flex-direction:column;text-align:left}.landingPage .text-wrapper p{width:100%;font-family:'Catamaran', sans-serif;text-align:left;font-size:40px;margin-bottom:-30px;font-weight:500;text-shadow:0px 3px 2px #000;color:#ececec}.landingPage .text-wrapper h1{width:100%;background-image:linear-gradient(110deg, rgba(85,101,203,0.5) 0%, #fff 100%);color:transparent;-webkit-background-clip:text;font-size:80px;margin:0}@media (max-width: 70rem){body{background:radial-gradient(800px at 120% 120%, #212750 10%, #020409 100%)}.landingPage .text-wrapper{padding-left:5rem}.landingPage .text-wrapper p{font-size:25px;margin-bottom:-18px}.landingPage .text-wrapper h1{font-size:50px}}@media (max-width: 40rem){.landingPage .text-wrapper{padding-left:3rem}.landingPage .text-wrapper p{font-size:25px;margin-bottom:-14px}.landingPage .text-wrapper h1{font-size:35px}}@media (max-width: 30rem){.landingPage .text-wrapper{padding-left:1rem}}.particles{position:absolute;top:0;left:0;bottom:0;right:0;z-index:-99999}::-webkit-scrollbar{width:4px}::-webkit-scrollbar-track{box-shadow:inset 0 0 5px rgba(73,73,73,0.3);border-radius:10px}::-webkit-scrollbar-thumb{background:#fff;border-radius:10px}

header{position:absolute;z-index:99;width:100%;padding:30px;background-color:rgba(250,250,250,0.05);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);font-size:20px}header .header-inner{display:flex;align-items:center;justify-content:space-between}header .header-inner nav ul{margin:0;padding:0;display:flex}header .header-inner nav ul li{list-style:none}header .header-inner nav ul li p{text-transform:capitalize;margin:0 40px;color:#ececec;transition:all 0.5s ease}header .header-inner nav ul li p:hover{color:#a2b3dd;transition:all 0.5s ease}@media (max-width: 85rem){header{display:flex;align-items:center;justify-content:space-evenly}}

.timeline-container{margin-bottom:50px;width:100%}.timeline{padding-top:0rem;font-family:'Catamaran', sans-serif;font-size:20px;color:white;padding-bottom:3rem}.timeline h3{font-size:22px}.timeline h5{font-family:'Abel', sans-serif;margin-top:-10px}.vertical-timeline-element-content{background:linear-gradient(110deg, rgba(85,101,203,0.2) 0%, rgba(255,255,255,0.1) 100%);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);border:2px solid rgba(255,255,255,0);text-shadow:0px 0px 0px rgba(255,255,255,0);box-shadow:0 8px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.15);transition:border 0.8s ease-in-out}.vertical-timeline-element-content:hover{border:2px solid rgba(255,255,255,0.2);transition:border 0.5s ease-in-out}.vertical-timeline-element-content-arrow{border-right:8px solid rgba(195,195,198,0.4)}.vertical-timeline::before{background:rgba(255,255,255,0);border-radius:100px}.vertical-timeline-element-icon{box-shadow:0 0 0 1.5px #fff,0px 0px 20px 0 rgba(255,255,255,0.35)}@media (max-width: 73rem){.vertical-timeline::before{background:rgba(255,255,255,0)}}

.about{width:100%;background-color:rgba(250,250,250,0.05);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);margin-bottom:100px;padding-bottom:20px;display:flex;align-items:center;flex-direction:column}.about-grid-container{width:auto;display:grid;grid-template-columns:auto;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem;grid-template-areas:'profilephoto aboutmetitle' 'profilephoto aboutmetext' 'profilephoto cvbutton';justify-items:stretch;text-align:justify;padding:3rem}.about-grid-item{text-align:left;width:100%;margin:auto auto;color:#FFF}.about-profile-photo{grid-area:profilephoto;display:flex;align-items:center;flex-direction:column}.about-profile-photo img{border-radius:3%;box-shadow:0 4px 18px 0 rgba(0,0,0,0.2),0 16px 40px 0 rgba(0,0,0,0.9);background-size:cover;background-position:center;width:100%;height:100%;max-width:250px;max-height:250px}.about-title{grid-area:aboutmetitle;text-align:left;background-image:linear-gradient(110deg, rgba(85,101,203,0.5) 0%, #fff 100%);color:transparent;-webkit-background-clip:text;font-size:80px;margin-bottom:-30px}.about-description{grid-area:aboutmetext;text-align:justify}.about-description p{margin:0;max-width:600px}.about-btn{grid-area:cvbutton;width:180px;height:auto;color:#fff;border-radius:5px;padding:10px 25px;font-weight:500;background:transparent;font-weight:900;cursor:pointer;transition:all 0.3s ease;position:relative;display:inline-block;outline:none;margin-left:0rem;margin-top:10px;text-decoration:none;display:flex;align-items:center;flex-direction:column}.about-btn p{margin:0}.btn{background:linear-gradient(110deg, rgba(85,101,203,0.5) 0%, rgba(160,173,254,0.5) 100%);border:none;z-index:1}.btn:after{position:absolute;content:"";width:0;height:100%;top:0;right:0;z-index:-1;background-color:#dedede;border-radius:5px;color:rgba(85,101,203,0.5);transition:all 0.5s ease}.btn:hover{color:#5565cb}.btn:hover:after{left:0;width:100%}.btn:active{top:2px}@media (max-width: 85rem){.about-grid-container{grid-column-gap:0rem;-webkit-column-gap:0rem;column-gap:0rem;grid-template-columns:repeat(auto-fill, minmax(100%, 1fr));width:auto;grid-template-areas:'aboutmetitle' 'aboutmetext' 'cvbutton'}.about-title{font-size:50px;margin-bottom:-10px}.about-title-footer{display:block}.about-grid-item{text-align:left}.about-profile-photo img{display:none}.about-btn{font-size:0.9rem;width:150px}.about-description{text-align:justify}.about-description p{font-size:15px}}

.social-container{background:linear-gradient(0deg, rgba(85,101,203,0.2) 0%, rgba(255,255,255,0.1) 100%);color:white;padding:1px 40px;margin-top:5rem;text-align:center;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);font-size:17px}a.social{margin:0 1rem;margin-top:1rem;transition:-webkit-transform 250ms;transition:transform 250ms;transition:transform 250ms, -webkit-transform 250ms;display:inline-block;color:#ffffff}a.social:hover{-webkit-transform:translateY(-4px);transform:translateY(-4px)}.social-container hr{margin-top:1.5rem;width:350px;border:1px solid rgba(255,255,255,0.3)}.social-container p{color:rgba(255,255,255,0.9)}@media (max-width: 85rem){.social-container{font-size:16px}}@media (max-width: 50rem){.social-container{font-size:14px}.social-container hr{width:50%;min-width:250px}}

.layers{width:100%;max-width:600px;margin:0 auto;padding:2rem;overflow:visible;display:flex;align-items:center;padding-bottom:5rem;padding-top:3.5rem}.layers g{-webkit-transform:translateX(var(--offset-x)) translateY(var(--offset-y)) translateZ(var(--offset-z));transform:translateX(var(--offset-x)) translateY(var(--offset-y)) translateZ(var(--offset-z));transition:-webkit-transform 0.2s ease-out;transition:transform 0.2s ease-out;transition:transform 0.2s ease-out, -webkit-transform 0.2s ease-out;-webkit-animation:hover 2s infinite;animation:hover 2s infinite}.layers g:nth-of-type(1):hover,.layers g:nth-of-type(2):hover,.layers g:nth-of-type(3):hover,.layers g:nth-of-type(4):hover,.layers g:nth-of-type(5):hover,.layers g:nth-of-type(6):hover,.layers g:nth-of-type(7):hover,.layers g:nth-of-type(8):hover,.layers g:nth-of-type(9):hover,.layers g:nth-of-type(10):hover,.layers g:nth-of-type(11):hover,.layers g:nth-of-type(12):hover{-webkit-transform:translateX(var(--offset-x)) translateY(calc(var(--offset-y) - 2px)) translateZ(var(--offset-z));transform:translateX(var(--offset-x)) translateY(calc(var(--offset-y) - 2px)) translateZ(var(--offset-z))}.layers text{font-weight:bold;-webkit-transform:skew(-68deg, 22deg) translate(calc(49px * var(--size) / 100), calc(7px * var(--size) / 100)) scaleY(0.5);transform:skew(-68deg, 22deg) translate(calc(49px * var(--size) / 100), calc(7px * var(--size) / 100)) scaleY(0.5);text-shadow:0px 0px 20px rgba(255,255,255,0.8);fill:#fff}.layers path:nth-child(2){fill:rgba(0,0,0,0.5)}.layers path:nth-child(3){fill:rgba(255,255,255,0.3)}

.gameDetailsRightContainer,.gameDetailsLeftContainer{width:100%;height:100%}.gameDetailsLeftContainer{background:linear-gradient(-90deg, #11171a 0%, #0b0e12 0%, rgba(0,0,0,0) 58%, rgba(0,0,0,0) 100%)}.gameDetailsRightContainer{background:linear-gradient(90deg, #11171a 0%, #0b0e12 0%, rgba(0,0,0,0) 58%, rgba(0,0,0,0) 100%)}.gameDetailsLeft,.gameDetailsRight{font-size:5rem;color:#fff;width:50%;max-width:50%;height:100%;padding-left:2rem;padding-right:2rem;flex-direction:row;display:flex;align-items:center;justify-content:center}.gameDetailsLeft{float:right}.gameDetailsRight{float:left}.gameDetailsRight p,.gameDetailsLeft p{font-size:1.3rem;text-align:justify;text-shadow:0px 5px 5px rgba(0,0,0,0.4);padding-left:6rem;padding-right:6rem}.gameDetailsRight img,.gameDetailsLeft img{width:100%;max-width:70%}.gameDetailsRight .gitHub-btn,.gameDetailsLeft .gitHub-btn{font-family:'Abel', sans-serif;display:inline-block;font-size:1.3rem;color:rgba(255,255,255,0.863);background:rgba(252,242,208,0.05);width:30%;text-align:center;text-decoration:none;border-width:2px;border-style:solid;border-radius:10px;padding:.45em;-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px)}.gameDetailsRight .gitHub-btn:hover,.gameDetailsLeft .gitHub-btn:hover{background:rgba(252,242,208,0.2)}.scrollArea{position:absolute;top:0;left:0;width:100vw;height:100vh;overflow-y:scroll;overflow-x:hidden}.loading{position:absolute;top:0px;left:0px;width:100%;height:100%;background:#000000;display:flex;align-items:center;justify-content:center;z-index:1000}.loading-bar-container{width:100px;height:20px;border-radius:5px;background:#272727}.loading-bar{height:20px;border-radius:5px;background:#ffffff}@media (max-width: 90rem){.gitHub-btn{font-size:1.6rem}}@media (max-width: 60rem){.gitHub-btn{font-size:1.2rem}}@media (max-width: 40rem){.skills-bg{width:300px;height:300px}}@media (max-width: 1440px){.gameDetailsRight p,.gameDetailsLeft p{font-size:1.2rem}}@media (max-width: 720px){.gameDetailsRight p,.gameDetailsLeft p{font-size:1rem}}@media (max-width: 900px){.gameDetailsLeftContainer,.gameDetailsRightContainer{background:linear-gradient(0deg, #000 0%, #000 0%, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 0%);height:100%}.gameDetailsLeft,.gameDetailsRight{padding-top:0rem;font-size:1rem;width:100%;max-width:100%;display:grid}.gameDetailsLeft,.gameDetailsRight{float:none}.gameDetailsLeft div,.gameDetailsRight div{background:linear-gradient(0deg, #080a0b 0%, #1e2730 50%, rgba(0,0,0,0) 100%, rgba(0,0,0,0) 100%);border-radius:0px 0px 10px 10px;padding:1rem;padding-bottom:2rem}.gameDetailsRight p,.gameDetailsLeft p{font-size:1rem;float:none;text-align:left;padding-left:3rem;padding-right:3rem;padding-bottom:1rem;padding-top:0rem}.gameDetailsRight img,.gameDetailsLeft img{width:70%;max-width:100%;float:none}.gameDetailsRight .gitHub-btn,.gameDetailsLeft .gitHub-btn{font-size:1.2rem;width:30%;height:25%;float:none}}@media (max-width: 400px){.gameDetailsRight img,.gameDetailsLeft img{width:80%;max-width:100%;float:none}.gameDetailsRight p,.gameDetailsLeft p{font-size:1rem;float:none;padding-left:1.5rem;padding-right:1.5rem}}

